@import "global";
@import "colors";
@import "mixins";
@import "typography";
@import "icons";
@import "fonts";
@import "header";
@import "footer";

html,
body {
    height: 100%;
    height: 100%;
}

body {
    position: relative;
    font-family: "Roboto" sans-serif;
}

// grid
.grid-container {
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
    grid-template-areas: 
        "header-box header-box header-box header-box" 
        "banner-box banner-box banner-box banner-box"
        "banner-box banner-box banner-box banner-box"
        "banner-box banner-box banner-box banner-box"
        "arrow-down-box arrow-down-box arrow-down-box arrow-down-box";

    background: url("./../src/img/laptop-glasses.jpeg") center;
    background-size: cover;
    background-repeat: no-repeat;

    position: relative;
    z-index: 4;

    &::after {
        content: "";
        background-color: rgba(12, 12, 12, 0.5);
        position: absolute;
        z-index: 4;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
}
  
.banner-box { 
    grid-area: banner-box;
    display: flex;
    justify-content: center;
    align-items: center;

    .wrapper-inner {
        .banner-box__content {
            flex-direction: column;
            display: flex;
            position: relative;
            z-index: 5;
            color: $white;

            .cta {
                background: rgba(255, 255, 255, 0.7);
                color: #000;
                text-decoration: none;
                font-size: 30px;
                text-transform: uppercase;
                padding: .4em;
                transition: all .1s ease-in-out;
                cursor: pointer;
                display: flex;
                justify-content: center;
                align-items: center;
                font-family: ns, Montserrat, sans-serif;
                overflow: hidden;

                .svg__icon--color {
                    fill: #000;
                    padding: 0 0 0 10px;
                }

                &:hover {
                    color: $blue;
                    background: #000;
                    &:hover .svg__icon--color {
                        fill: $blue;
                    }
                }
            }

            h1 {
                font-family: ns, Roboto, sans-serif;
                text-transform: uppercase;
                font-size: 60px;
                order: -1;
                padding: 0 0 2px;

                span {
                    color: #eee;
                    display: inline-block;
                    background: rgba(0, 0, 0, 0.7);
                    margin-bottom: 5px;
                    padding: 0 .2em;
                    transition: all .1s ease-in-out;
                }
            }
        }

        // banner text hover
        .banner-box__content .cta:hover ~ h1 span {
            color: #000;
            background: #fff;
        }
    }
}
  
.arrow-down-box { 
    grid-area: arrow-down-box;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 5;
    bottom: 0;
}

.about-box {
    .about-box__content,
    .wrraper-inner {

        .about-box__content--items {
            position: relative;
            .navigation-custom-container {
                position: absolute;
                top: 115px;
                right: -120px;
                @include flexCHV;
                transform: rotate(90deg);
                background: #858585;
                padding: 5px;

                .right-nav-custom.slick-arrow, 
                .left-nav-custom.slick-arrow {
                    @include flexCHV;
                    cursor: pointer;
                    background-color: $white;
                }
            }

            .my-pagination {
                .slick-dots  {

                    li button:before {
                        color: #a0765b;
                        font-size: 10px;
                    }

                    li.slick-active {
                        button:before {
                            color: $blue;
                            font-size: 14px;
                        }
                    }
                }
            }

        }
    }
}

.gallery-box {
    padding: 10px 0;

    .gallery-box__items {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(4, 1fr);
        grid-gap: 5px;

        .div1 { grid-area: 1 / 1 / 2 / 2; }
        .div2 { grid-area: 1 / 2 / 2 / 3; }
        .div3 { grid-area: 1 / 3 / 2 / 4; }
        .div4 { grid-area: 1 / 4 / 2 / 5; }
        .div5 { grid-area: 2 / 4 / 3 / 5; }
        .div6 { grid-area: 3 / 4 / 4 / 5; }
        .div7 { grid-area: 4 / 4 / 5 / 5; }
        .div8 { grid-area: 4 / 3 / 5 / 4; }
        .div9 { grid-area: 4 / 2 / 5 / 3; }
        .div10 { grid-area: 4 / 1 / 5 / 2; }
        .div11 { grid-area: 3 / 1 / 4 / 2; }
        .div12 { grid-area: 2 / 1 / 3 / 2; }
        .div13 { grid-area: 2 / 2 / 4 / 4; }

        .gallery-box__items-item {
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            a {
                img {
                    max-width: 100%;
                    height: auto;
                }
            }
        }

        .gallery-box__items-item::after {
            content: "";
            position: absolute;
            // background-color: rgba(12,12,12,.5);
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            cursor: pointer;
        }

        // .gallery-box__items-item::after {
        //     &:hover {
        //         background-color: blueviolet;
        //     }
        // }


    }
}



